import { Button, Tooltip } from "antd";
import { BsCheckLg } from "react-icons/bs";

function ButtonValidate({ row, onClick }) {
	return (
		<Tooltip title="Validar">
			<Button
				type="text"
				disabled={row?.estado === "pagado"}
				onClick={onClick}
			>
				<BsCheckLg />
			</Button>
		</Tooltip>
	);
}
export default ButtonValidate;
