import { Button } from "antd";
import dayjs from "dayjs";
import React, { createRef } from "react";
import { AiFillPrinter } from "react-icons/ai";
import { useReactToPrint } from "react-to-print";
import { DocumentExtraordinarioPrint } from "../planillas/historial-pago/DocumentExtraordinarioPrint";

export const ButtonPrintExtraordinario = ({ data }) => {
	console.log(
		"🚀 ~ file: ButtonPrintExtraordinario.jsx:9 ~ ButtonPrintExtraordinario ~ data:",
		data
	);
	const fechaActual = dayjs().format("YYYYMMDD_HHMM");
	const ComponenteAImprimir = React.forwardRef((props, ref) => (
		<div ref={ref}>
			{props.data.pago.destino_pagos?.map((des, index) => (
				<DocumentExtraordinarioPrint
					data={data}
					destino={des.destino}
					key={index}
					index={
						props.data.pago.destino_pagos?.length > 1
							? index + 1
							: 0
					}
				/>
			))}
		</div>
	));

	const componentRef = createRef();
	const handlePrint = useReactToPrint({
		content: () => componentRef.current,
		removeAfterPrint: true,
		documentTitle: `RECIBO_DE_PAGO_${fechaActual}_${data.index}`,
	});

	return (
		<>
			<div style={{ display: "none" }}>
				<ComponenteAImprimir ref={componentRef} data={data} />
			</div>
			{/* <Tooltip placement="top" title="Imprimir"> */}
			<Button
				type="text"
				style={{
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
				}}
				icon={<AiFillPrinter style={{ fontSize: "20px" }} />}
				onClick={handlePrint}
			/>
			{/* </Tooltip> */}
		</>
	);
};
