import { Button } from "antd";
import dayjs from "dayjs";
import React, { createRef } from "react";
import { AiFillPrinter } from "react-icons/ai";
import { useReactToPrint } from "react-to-print";
import { DocumentPrint } from "../planillas/historial-pago/DocumentPrint";

export const ButtonPrint = ({ data }) => {
	const fechaActual = dayjs().format("YYYYMMDD_HHMM");
	const ComponenteAImprimir = React.forwardRef((props, ref) => (
		<div ref={ref}>
			{props.data.destino?.map((des, index) => (
				<DocumentPrint
					data={data}
					destino={des}
					key={index}
					index={props.data.destino?.length > 1 ? index + 1 : 0}
				/>
			))}
			{/* <DocumentPrint data={props.data} /> */}
		</div>
	));

	const componentRef = createRef();
	const handlePrint = useReactToPrint({
		content: () => componentRef.current,
		removeAfterPrint: true,
		documentTitle: `RECIBO_DE_PAGO_${fechaActual}_${data.index}`,
	});

	return (
		<>
			<div style={{ display: "none" }}>
				<ComponenteAImprimir ref={componentRef} data={data} />
			</div>
			{/* <Tooltip placement="top" title="Imprimir"> */}
			<Button
				type="text"
				style={{
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
				}}
				icon={<AiFillPrinter style={{ fontSize: "20px" }} />}
				onClick={handlePrint}
			/>
			{/* </Tooltip> */}
		</>
	);
};
